import React from 'react'
import {Card, Alert, Divider, Select, Steps, Input, Button, Form,Icon,BackTop} from 'antd'
import {inject,observer} from 'mobx-react'
import CustomBreadcrumb from '../../../components/CustomBreadcrumb/index'
import './css/formDeni2.css'
import {digitUppercase} from '../../../utils/utils'
import TypingCard from '../../../components/TypingCard'

const { Step } = Steps;
const { Option } = Select;
const formItemLayout = {
  labelCol: {
    span: 5,
  },
  wrapperCol: {
    span: 19,
  },
};
const tailFormItemLayout = {
  wrapperCol:{
    offset:5
  }
}


@inject('stepFormStore') @Form.create() @observer
class Step1 extends React.Component{
  nextStep = () =>{
    this.props.form.validateFields((err,values)=>{
      if (!err){
        this.props.stepFormStore.setInfo(values)
        this.props.stepFormStore.setCurrent(1)
      }
    })
  }
  render(){
    const {getFieldDecorator} = this.props.form
    return (
      <div>
        <Form className='stepForm' hideRequiredMark>
          <Form.Item {...formItemLayout} label="">
            {getFieldDecorator('payAccount', {
              initialValue: 'ant-design@alipay.com',
              rules: [{ required: true, message: '' }],
            })(
              <Select placeholder="test@example.com">
                <Option value="ant-design@alipay.com">ant-design@alipay.com</Option>
              </Select>
            )}
          </Form.Item>
          <Form.Item {...formItemLayout} label="">
            <Input.Group compact>
              <Select defaultValue='alipay' style={{width:100}}>
                <Option value="alipay"></Option>
                <Option value="bank"></Option>
              </Select>
              {getFieldDecorator('receiverAccount',{
                  initialValue: 'test@example.com',
                  rules: [
                    { required: true, message: '' },
                    { type: 'email', message: '' },
                  ],
                })(
                  <Input style={{ width: 'calc(100% - 100px)' }}/>
                )}
            </Input.Group>
          </Form.Item>
          <Form.Item {...formItemLayout} label="">
            {getFieldDecorator('receiverName', {
              initialValue: 'Alex',
              rules: [{ required: true, message: '' }],
            })(<Input placeholder="" />)}
          </Form.Item>
          <Form.Item {...formItemLayout} label="">
            {getFieldDecorator('amount', {
              initialValue: 500,
              rules: [
                { required: true, message: '' },
                {
                  pattern: /^(\d+)((?:\.\d+)?)$/,
                  message: '',
                },
              ],
            })(<Input prefix="" placeholder="" />)}
          </Form.Item>
          <Form.Item {...tailFormItemLayout}>
            <Button type='primary' onClick={this.nextStep}></Button>
          </Form.Item>
        </Form>
        <Divider/>
        <div className='desc'>
          <h3></h3>
          <h4></h4>
          <p>
            
          </p>
          <h4></h4>
          <p>
            
          </p>
        </div>
        <BackTop visibilityHeight={200} style={{right: 50}}/>
      </div>
    )
  }
}

@inject('stepFormStore') @Form.create() @observer
class Step2 extends React.Component{
  state = {
    loading:false
  }
  handleSubmit = ()=>{
    this.props.form.validateFields((err,values)=>{
      if (!err){
        this.setState({
          loading:true
        })
       setTimeout(()=>{
         this.setState({
           loading:false
         })
         this.props.stepFormStore.setCurrent(2)
       },2000)
      }
    })
  }
  render(){
    const {getFieldDecorator} = this.props.form
    return (
      <div>
        <Form className='stepForm' id='step2'>
          <Alert closable showIcon message="" style={{ marginBottom: 24 }}/>
          <Form.Item {...formItemLayout} className='setFormText' label="">
            {this.props.stepFormStore.info.payAccount}
          </Form.Item>
          <Form.Item {...formItemLayout} label="">
            {this.props.stepFormStore.info.receiverAccount}
          </Form.Item>
          <Form.Item {...formItemLayout} className='setFormText' label="">
            {this.props.stepFormStore.info.receiverName}
          </Form.Item>
          <Form.Item {...formItemLayout} className='setFormText' label="">
            <span className='money'>{this.props.stepFormStore.info.amount}</span>
            <span>{digitUppercase(this.props.stepFormStore.info.amount)}</span>
          </Form.Item>
          <Divider/>
          <Form.Item {...formItemLayout} label="" required={false}>
            {getFieldDecorator('password', {
              initialValue: '123456',
              rules: [
                {
                  required: true,
                  message: '',
                },
              ],
            })(<Input type="password" autoComplete="off" style={{ width: '80%' }} />)}
          </Form.Item>
          <Form.Item
            style={{ marginBottom: 8 }}
            wrapperCol={{
              xs: { span: 24, offset: 0 },
              sm: {
                span: formItemLayout.wrapperCol.span,
                offset: formItemLayout.labelCol.span,
              },
            }}
            label=""
          >
            <Button type="primary" onClick={this.handleSubmit} loading={this.state.loading}></Button>
            <Button onClick={()=>this.props.stepFormStore.setCurrent(0)} style={{ marginLeft: 8 }}></Button>
          </Form.Item>
        </Form>
      </div>
    )
  }
}

@inject('stepFormStore') @observer
class Step3 extends React.Component{
  render(){
    return (
      <div id='step3'>
        <div>
          <div className='icon-box'>
            <Icon type='check-circle'/>
          </div>
          <div>
            <h3 className='success'></h3>
            <p className='success-desc'></p>
          </div>
          <Form className='result'>
            <Form.Item>
              <Form.Item {...formItemLayout} className='setFormText' label="">
                {this.props.stepFormStore.info.payAccount}
              </Form.Item>
              <Form.Item {...formItemLayout} style={{marginBottom:18}} label="">
                {this.props.stepFormStore.info.receiverAccount}
              </Form.Item>
              <Form.Item {...formItemLayout} className='setFormText' label="">
                {this.props.stepFormStore.info.receiverName}
              </Form.Item>
              <Form.Item {...formItemLayout} className='setFormText' label="">
                <span className='money'>{this.props.stepFormStore.info.amount}</span>
              </Form.Item>
            </Form.Item>
          </Form>
          <div>
            <Button type='primary' onClick={()=>this.props.stepFormStore.setCurrent(0)}></Button>
            <Button style={{ marginLeft: 8 }}></Button>
          </div>
        </div>
      </div>
    )
  }
}

@inject('stepFormStore') @observer
class FormDemo2 extends React.Component {
  showStep = ()=>{
    switch (this.props.stepFormStore.current){
      case 1 : return <Step2/>
      case 2: return <Step3/>
      default: return <Step1/>
    }
  }
  render () {
    return (
      <div>
        <CustomBreadcrumb arr={['', '','']}/>
        <TypingCard source=''/>
        <Card title='' bordered={false} style={{minHeight: 600}}>
          <Steps style={styles.steps} current={this.props.stepFormStore.current}>
            <Step title="" />
            <Step title="" />
            <Step title="" />
          </Steps>
          <div>{this.showStep()}</div>
        </Card>
      </div>
    )
  }
}

const styles = {
  steps:{
    maxWidth:750,
    margin: '16px auto'
  },
  desc:{
    padding:'0 56px',
  }
}

export default FormDemo2


